h2 {
    color: #fff;
}

.homeTitle {
    margin-bottom: 2rem;
    font-size: 24px;
}

.filtersContainer {
    position: absolute;
    top: 9.7rem;
    left: 3rem;
    color: white;
}

.filtersContainer legend{
    padding-bottom: 0.2rem;
    border-color: white;
    color: white;
    margin-bottom: 1px;
}

.filtersContainer p {
    width: 100%;
    text-align: center;
    border-radius: 0;
    padding: 0;
    margin: 1rem 0;
    border-bottom: 1px solid white;
}

.resetButton {
    background-color: transparent!important;
    color: white!important;
    margin: 0 0.5rem!important;
    z-index: 2;
}

.swipe {
    position: absolute;
}

.cardContainer {
    margin: 0 auto;
    width: 60vw;
    max-width: 260px;
    border-radius: 20px;
    height: 300px;
    box-shadow: 0px 0px 60px 0px rgba(0, 0, 0, 0.30);
}
.buttons button{
    color: #e66465 !important;
    background: rgb(225,225,225) !important;
}

.card {
    position: relative;
    background-color: #fff;
    width: 80vw;
    max-width: 260px;
    height: 300px;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}

.cardContent {
    width: 100%;
    height: 100%;
}

.card h3 {
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: #fff;
}

.infoText {
    width: 100%;
    height: 28px;
    justify-content: center;
    display: flex;
    color: #fff;
    animation-name: popup;
    animation-duration: 800ms;
}

.buttons {
    margin: 40px auto;
}

.buttons button {
    flex-shrink: 0;
    padding: 10px;
    border-radius: 5px;
    border: none;
    color: #fff;
    font-size: 18px;
    background-color: #9198e5;
    transition: 200ms;
    margin: 0 10px;
    font-weight: bolder;
    width: 160px;
    box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.10);
}

.buttons button:hover {
    transform: scale(1.05);
}

@keyframes popup {
    0% {
        transform: scale(1, 1)
    }

    10% {
        transform: scale(1.1, 1.1)
    }

    30% {
        transform: scale(.9, .9)
    }

    50% {
        transform: scale(1, 1)
    }

    57% {
        transform: scale(1, 1)
    }

    64% {
        transform: scale(1, 1)
    }

    100% {
        transform: scale(1, 1)
    }
}

.cardViewWrapper {
    width: 70%;
    margin: 0 auto;
}

.containerCardView {
    /* z-index: 2; */
    width: 100%;
    margin: 0 auto;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.item:hover {
    box-shadow: 0 3px 5px 2px rgba(255, 105, 135, .3);
}
.containerCardView .root {
    position: relative;
    background-color: rgba(255, 255, 255, 0.4);
    width: 250px;
    height: 350px;
    margin: 13px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.media {
    height: 240px;
}

.colorBlack {
    color: 'black';
}

.btnContainer {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.infoText {
    width: 80%;
    margin: 0 auto;
}